import React from "react";

export default function About() {
  return (
    <section id="about" className="bg-white">
      <div className="container mx-auto flex px-10 py-20 md:flex-row  bg-white flex-col items-center">
        <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
          <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900">
            Hi, I'm Asuman.
            <br className="hidden lg:inline-block" />I love to build interactive
            front-end applications.
          </h1>
          <p className="mb-8 leading-relaxed">
            While living a life intertwined with both art and technical fields,
            I always try to learn more. Besides working as a consultant and
            developer, I am trying to reveal my own art.
          </p>
          <div className="flex justify-center">
            <a
              href="#contact"
              className="inline-flex text-white bg-blue-900 border-0 py-2 px-6 focus:outline-none hover:bg-blue-800 rounded text-lg"
            >
              Work With Me
            </a>
            <a
              href="#projects"
              className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded text-lg"
            >
              See My Past Work
            </a>
          </div>
        </div>
        <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6">
          <img
            className="object-cover object-center rounded"
            alt="hero"
            src="./coding.webp"
          />
        </div>
      </div>
    </section>
  );
}
